@import '../../../../scss/vars.scss';

.slider {
  display: flex;
  align-items: center;

  @media (max-width: $breakpoint-md) {
    align-items: flex-start;
    flex-flow: column;
  }

  &__wrapper {
    position: relative;
    width: 710px;
    min-width: 200px;
    height: 10px;
    background-color: $color-third;
    border-radius: 5px;
    margin-right: 30px;

    @media (max-width: $breakpoint-sm) {
      max-width: 520px;
    }

    @media (max-width: $breakpoint-xs) {
      max-width: 500px;
    }

    @media (max-width: $breakpoint-xs) {
      max-width: 330px;
    }

    @media (max-width: $breakpoint-xxs) {
      max-width: 200px;
    }

    &--vertical {
      height: 710px;
      width: 10px;
      min-width: 0;
    }
  }

  &__wrapper-input {
    display: flex;
    flex-direction: column;

    @media (max-width: $breakpoint-md) {
      margin-top: 20px;
    }
  }

  &__title-input {
    font-size: $font-size--h3;
    font-family: $font-family;
    margin-left: 7px;
  }

  &__range {
    width: 40px;
    height: 20px;
    margin-right: 25px;
    font-family: $font-family;
    color: $dark-shade-75;
    border: none;
  }

  &__bar {
    position: absolute;
    height: 10px;
    margin-left: 10px;
    border: none;
    border-radius: 5px;
    background-color: $color-primary;

    &--vertical {
      width: 10px;
      right: 0.8px;
      margin-left: 0;
      margin-top: 10px;
    }

    &--single {
      margin-left: 0;

      &--vertical {
        margin-top: 0;
      }
    }
  }

  &__handle {
    position: relative;
    top: -6px;
    width: 25px;
    height: 25px;
    background-color: $color-primary;
    border-radius: 3px;
    border-radius: 50%;
    box-shadow: 0 0 0 0 rgba(98, 0, 238, 0.1);
    transition: box-shadow 0.3s ease-in-out;
    cursor: pointer;

    &:active {
      box-shadow: 0 0 0 10px rgba(98, 0, 238, 0.1);

      .slider__label {
        display: block;
        font-family: $font-family;
      }
    }

    &--from {
      &--vertical {
        margin: 0;
      }
    }

    &--to {
      top: -31px;

      &--vertical {
        margin-top: -25px;
      }
    }

    &--single {
      margin-left: -15px;

      &--vertical {
        margin-top: -20px;
      }
    }

    &--vertical {
      margin-left: -8.5083px;
    }
  }

  &__label {
    display: none;
    position: absolute;
    top: -40px;
    right: -3px;
    width: 30px;
    height: 30px;
    margin: 0;
    line-height: 30px;
    border-radius: 50% 50% 0 50%;
    background-color: $color-secondary;
    text-align: center;
    transform: rotate(45deg);
    pointer-events: none;
    user-select: none;
  }

  &__label-text {
    font-size: 11px;
    color: $dark-shade-100;
    transform: rotate(-45deg);
  }

  &--vertical {
    top: 0;
    right: 8px;
  }
}

.input {
  width: 100px;
  height: 35px;
  padding-left: 15px;
  margin: 5px;
  color: $dark-shade-75;
  border: 1px solid rgba(31, 32, 65, 0.25);
  border-radius: 4px;
}
